.split-screen {
    display: flex;
    height: 100vh;
  }
  
  .editor-pane {
    flex: 1;
    border-right: 1px solid #ddd;
    padding: 16px;
    background: #f9f9f9;
    display: flex;
    flex-direction: column;
  }
  
  .form-pane {
    flex: 1;
    padding: 16px;
    background: #ffffff;
  }
  
  .pane-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 16px;
  }
  
  .error-message {
    color: red;
    margin-top: 8px;
    font-size: 0.9rem;
  }
  
  .form-container {
    max-width: 600px;
    margin: 0 auto;
  }
  